.movies .movie {
    /* width: 45%; */
    background-color: #DDD8D5;
    margin-bottom: 70px;
    /* display: flex; */
    /* align-items: flex-start; */
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #0e1110;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
    display: grid;
    grid-template-columns: minmax(150px, 1fr) ;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
  }

.movie img {
    position: relative;
    top: -50px;
    max-width: 140px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 60px -12px rgba(50, 50, 93, 0.25), 
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    /* flex-wrap: wrap; */
    margin: 5px 0px;
    color: #348498;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
} 

.movie .movie__year {
    color: #738598;
}

.movies__movie {
    display: flex;
    align-items: flex-start;
}
